/*========================================================
                        
=========================================================*/

body {
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    background-color: #fff;
    color: #333;
}

h1,
h2,
h3,
h4 {
    margin-top: 0;
    font-weight: bold;
}

a {
    color: inherit;
    text-decoration: none;
    &:link,
    &:active,
    &:visited,
    &:focus {
        color: inherit;
    }
    &:hover {
        color: #2d9ed6;
        text-decoration: none;
        @media( min-width:768px ){
            img {
                filter: alpha(opacity=80);
                -moz-opacity: 0.8;
                opacity: 0.8;
            }
        }
    }
}

.container {
    width: 100%;
    max-width: 1130px;
    &.w_long {
        max-width: 1830px;
    }
}

.en{
    font-family: 'Roboto Condensed', sans-serif;
}

h2{
    padding-bottom:10px;
    border-bottom:solid 1px #ddd;
}

table>tbody>tr>th{
    white-space:nowrap;
}